.tg-faq-section {
    display: flex;
    background-color: #0c0f25;
    color: white;
    padding: 80px 78px;
    font-family: 'Poppins', sans-serif;
    justify-content: space-between;
    gap: 60px;
    flex-wrap: wrap;
}
  
.tg-faq-left {
    flex: 1;
    max-width: 500px;
}
  
.tg-faq-left h5 {
    color: #EE6A4D;
    text-transform: uppercase;
    font-size: 20px;
    display: flex;
    font-weight: 500;
    margin-bottom: 20px;
}
  
.tg-faq-left h2 {
    font-size: 52px;
    font-weight: bold;
    color: #fff;
    display: flex;
    line-height: 0.9;
    margin-bottom: 10px;
    font-family: 'DM Serif Display', serif;
}
  
.tg-faq-left p {
    font-size: 16px;
    font-weight: 500;
    color: #d0d3f2;
    margin-bottom: 30px;
    display: block;
    text-align: left;
}
  
.tg-faq-left button {
    background: #D8583F;
    color: white;
    font-weight: 600;
    font-size: 15px;
    border: none;
    padding: 14px 24px;
    display: flex;
    border-radius: 8px;
    cursor: pointer;
}
  
.tg-faq-right {
    flex: 1.2;
    display: flex;
    flex-direction: column;
    gap: 18px;
    max-width: 750px;
}
  
.tg-faq-item {
    background: transparent;
    border-bottom: 1px solid #2e314e;
    padding-bottom: 14px;
    cursor: pointer;
    transition: all 0.3s ease;
}
  
.tg-faq-item.open .tg-faq-answer {
    margin-top: 10px;
    font-size: 15px;
    color: #d0d3f2;
    line-height: 1.6;
    display: block;
    text-align: left;
}
  
.tg-faq-question {
    font-size: 20px;
    font-weight: 500;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
}
  
.tg-faq-toggle {
    font-size: 26px;
    font-weight: 700;
    color: white;
}
  
@media (max-width: 1024px) {
    .tg-faq-section {
      flex-direction: column;
      padding: 60px 30px;
    }
  
    .tg-faq-left,
    .tg-faq-right {
      max-width: 100%;
    }
  
    .tg-faq-left h2 {
      font-size: 36px;
    }
}
  
@media (max-width: 600px) {
  .category-buttons {
    display: flex;
    flex-wrap: nowrap;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    width: 100%;
    padding-bottom: 8px;
  }
  
  .category-buttons::-webkit-scrollbar {
    display: none;
  }
  
  .chip {
    flex: 0 0 auto;
    margin-right: 8px;
  }
  
  .blog-filter-container {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .search-box {
    width: 100% !important;
    max-width: 100% !important;
    margin-top: 10px;
  }
}
  
.blog-grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
  padding: 20px 0;
}
  
.blog-scroll-container::-webkit-scrollbar {
  display: none;
}
  
@media (max-width: 600px) {
  .blog-contenting h1 {
    font-size: 33px !important;
    line-height: 1.2 !important;
    margin-top: -10px;
    white-space: nowrap !important;
    overflow: hidden !important;
    text-overflow: ellipsis !important;
    /* text-align: center !important;  */
    margin-left: 0px;
  }
} /*  Missing braces added: closes @media */






