/* why.css */

.why-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 30px;
  padding: 60px 40px;
  background-color: #0c1025;
  font-family: 'Manrope', sans-serif;
  flex-wrap: wrap;
  color: #fff;
}

.why-left {
  flex: 1;
  max-width: 420px;
  color: black;
}

.why-heading {
  font-size: 25px;
  font-weight: 600;
  margin-bottom: 20px;
  font-family: "manrope", sans-serif;
  color: #EE6A4D;
}

.why-description {
  font-size: 13px;
  line-height: 1.6;
  margin-bottom: 30px;
  font-family: "manrope", sans-serif;
  font-weight: 600;
  color: #d1d1d1;
}

.why-button {
  background-color: #FFFFFF;
  color: #EE6A4D;
  border: none;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: 700;
  font-family: "manrope", sans-serif;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.why-button:hover {
  background-color: #e65c30;
}

.why-right {
  flex: 2;
  display: grid;
  background-color: white ;
  grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
  gap: 20px;
  align-items: flex-start;
  position: relative;
  left: 40px;
}

.why-card {
  background-color: white;
  border-radius: 12px;
  overflow: hidden;
  text-align: center;
  padding: 20px 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
  transition: transform 0.3s;
}

.why-card:hover {
  transform: translateY(-5px);
}

.why-image {
  width: 100%;
  height: auto;
  max-height: 160px;
  object-fit: contain;
  border-radius: 10px;
  margin-bottom: 12px;
}

.why-name {
  font-size: 16px;
  font-weight: 600;
  color:black;
  margin-bottom: 6px;
}

.why-percentile {
  font-size: 15px;
  color: #7383ff;
  font-weight: 500;
}

/* Responsive */
@media (max-width: 768px) {
  .why-container {
    flex-direction: column;
    padding: 40px 20px;
  }

  .why-left {
    max-width: 100%;
    margin-bottom: 30px;
  }

  .why-right {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    margin-bottom: 25px;
  }
}










