  flex: 0 0 auto;                /* shrink na ho */
  width: 200px;                  /* card width (tune kar sakte ho) */
  scroll-snap-align: start;      /* snap per card */
}

/* Image responsive + consistent height */
.why-image {
  width: 100%;
  height: 160px;                 /* consistent tile height */
  object-fit: cover;
  border-radius: 10px;
  display: block;
}

/* Mobile tweaks */
@media (max-width: 768px) {
  .why-card { width: 180px; }
  .why-image { height: 140px; }
}
