.catresults-container {
  padding: 60px;
  background-color: #ffffff;
  font-family: "Clash Display", serif;
}

/* Top Row for heading + stats side by side */
.catresults-top-row {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  flex-wrap: wrap;
}

/* Left Heading Block */
.catresults-heading-block {
  flex-shrink: 0;
}

.catresults-year {
  font-size: 24px;
  font-weight: 400;
  font-family: "Manrope", sans-serif;
  color: #1a237e;
  display: flex;
}

.catresults-year .arrow {
  font-size: 24px;
  margin-left: 4px;
  font-weight: 400;
  font-family: "Manrope", sans-serif;
  color: #1a237e;
}

.catresults-title {
  font-size: 58px;
  font-weight: 400;
  color: #070a22;
  margin-top: 5px;
  text-align: left;
  font-family: DM serif Display;
}

/* Right Stats Grid */
.catresults-stats {
  display: flex;
  gap: 80px;
  align-items: center;
  flex-wrap: wrap;
}

.catresults-box {
  text-align: center;
  min-width: 100px;
}

.catresults-number {
  font-size: 38px;
  font-weight: 700;
  color: #f25c54;
  font-family: "Manrope", sans-serif;
}

.catresults-label {
  font-size: 24px;
  font-weight: 500;
  color: #070a22;
  font-family: "Manrope", sans-serif;
  margin-top: 6px;
}

.tt-student-showcase-wrapper {
  padding: 0 60px;
  background-color: #ffffff;
}

.tt-student-cards-scroll {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  gap: 20px;
  padding-bottom: 10px;
  scroll-behavior: smooth;
}

.tt-student-cards-scroll::-webkit-scrollbar {
  display: none; /* Hide scrollbar for Chrome, Safari */
}

.tt-student-cards-scroll {
  -ms-overflow-style: none; /* Hide scrollbar for IE/Edge */
  scrollbar-width: none; /* Hide scrollbar for Firefox */
}

.tt-student-card {
  flex: 0 0 180px;
  width: 180px;
  min-height: 360px;
  position: relative;
  overflow: hidden;
  border-radius: 16px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  background: #fff;
  transition: transform 0.3s ease;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.tt-student-card:hover {
  transform: translateY(-4px);
}

.tt-student-card img {
  width: 100%;
  height: 300px;
  object-fit: cover;
  border-radius: 16px 16px 0 0;
  flex-shrink: 0;
}

.tt-student-name {
  position: absolute;
  left: 16px;
  bottom: 60px;
  display: none;
  color: white;
  font-weight: 600;
  font-size: 16px;
  z-index: 1;
}

.tt-student-percentile {
  margin-top: 10px;
  font-weight: 700;
  font-size: 18px;
  color: #1b1ba3;
  padding: 0 16px 16px;
}

.tt-student-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 280px;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.2));
  color: white;
  padding: 20px;
  opacity: 0;
  transition: opacity 0.3s ease;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.tt-student-card:hover .tt-student-overlay {
  opacity: 1;
}

.tt-student-overlay h4 {
  font-size: 18px;
  margin-bottom: 8px;
  font-weight: 700;
}

.tt-student-overlay p {
  font-size: 14px;
  line-height: 1.4;
}

.tt-overlay-content {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 100%;
  height: 100%;
}

.tt-student-footer-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 30px;
}

.tt-student-nav {
  display: flex;
  align-items: center;
  margin-top: -50px;
  /* gap: 16px; */
  /* margin-left: 50px; */
}

.tt-circle-btn {
  /* background: #ea580c; */
  border: none;
  border-radius: 50%;
  background-color: white;
  color: #000;
  width: 40px;
  height: 30px;
  font-size: 23px;
  border: 1px solid black;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.tt-dot {
  width: 90px;
  height: 4px;
  background: #ddd;
  border-radius: 2px;
}

.tt-dot.active {
  /* background: #f25c54; */
}

.tt-view-all {
  border: 2px solid #f25c54;
  background: white;
  color: #f25c54;
  padding: 8px 16px;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.tt-view-all:hover {
  background: #f25c54;
  color: white;
}

/* RESPONSIVE FIXES - Only changes below this line */

@media (max-width: 1024px) {
  .catresults-container {
    padding: 40px 30px;
  }

  .tt-student-showcase-wrapper {
    padding: 0 30px;
  }

  .catresults-title {
    font-size: 42px;
    text-align: left;
  }

  .catresults-number {
    font-size: 42px;
  }

  .catresults-label {
    font-size: 18px;
  }

  .catresults-stats {
    gap: 40px;
  }

  .tt-student-card {
    flex: 0 0 250px;
    width: 250px;
  }

  .tt-student-card img,
  .tt-student-overlay {
    height: 260px;
  }

  .tt-student-name {
    font-size: 15px;
    bottom: 55px;
    left: 12px;
  }

  .tt-student-percentile {
    font-size: 16px;
    padding: 10px 14px;
  }
}

@media (max-width: 768px) {
  .catresults-container {
    padding: 40px 20px;
  }

  .tt-student-showcase-wrapper {
    padding: 0 20px;
  }

  .catresults-top-row {
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
  }

  .catresults-title {
    font-size: 32px;
    text-align: center;
    line-height: 1.2;
    margin-bottom: 10px;
  }

  .catresults-stats {
    width: 100%;
    justify-content: center;
    gap: 24px;
    flex-wrap: nowrap;
    overflow-x: auto;
    scroll-snap-type: x mandatory;
    -webkit-overflow-scrolling: touch;
    padding-bottom: 10px;
  }

  .catresults-box {
    flex: 0 0 auto;
    min-width: 100px;
    scroll-snap-align: start;
    text-align: center;
  }

  .catresults-number {
    font-size: 28px;
  }

  .catresults-label {
    font-size: 16px;
  }

  .tt-student-footer-row {
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    gap: 16px;
    margin-top: 20px;
  }

  .tt-student-nav {
    margin-left: 0;
    display: none;
  }

  .tt-student-card {
    flex: 0 0 240px;
    width: 240px;
    min-height: 320px;
  }

  .tt-student-card img,
  .tt-student-overlay {
    height: 240px;
  }

  .tt-student-name {
    font-size: 14px;
    bottom: 50px;
  }

  .tt-student-percentile {
    font-size: 15px;
    padding: 8px 12px;
  }

  .tt-view-all {
    padding: 8px 16px;
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  .catresults-container {
    padding: 30px 16px;
  }

  .tt-student-showcase-wrapper {
    padding: 0 16px;
  }

  .catresults-title {
    font-size: 28px;
    margin-top: -10px;
    text-align: center;
  }

  .catresults-year {
    font-size: 18px;
  }

  .catresults-number {
    font-size: 24px;
  }

  .catresults-label {
    font-size: 14px;
    margin-top: -40px;
  }

  .catresults-stats {
    /* gap: 16px; */
    margin-left: -30px;
     margin-top: -30px; 
    
  }

  .catresults-box {
    min-width: 80px;
  }

  .tt-student-card {
    flex: 0 0 220px;
    width: 220px;
    min-height: 300px;
  }

  .tt-student-card img,
  .tt-student-overlay {
    height: 220px;
  }

  .tt-student-name {
    font-size: 13px;
    bottom: 45px;
    left: 12px;
  }

  .tt-student-percentile {
    font-size: 14px;
    padding: 8px 12px;
  }

  .tt-student-overlay h4 {
    font-size: 16px;
  }

  .tt-student-overlay p {
    font-size: 13px;
  }

  .tt-view-all {
    padding: 6px 12px;
    font-size: 13px;
  }

  .tt-circle-btn {
    width: 36px;
    height: 36px;
    font-size: 16px;
  }

  .tt-dot {
    width: 30px;
  }
}

/* container */
.tt-student-cards-scroll{ display:flex; flex-wrap:nowrap; overflow-x:auto; gap:20px; }

/* card */
.tt-student-card{
  flex:0 0 180px; width:180px; background:#fff; border-radius:16px;
  box-shadow:0 5px 10px rgba(0,0,0,.1); overflow:hidden; display:flex; flex-direction:column;
  transition:transform .3s ease;
}
.tt-student-card:hover{ transform: translateY(-4px); }

/* media (image + overlays only in this area) */
.tt-media{ position:relative; width:100%; height:300px; }
.tt-media img{ width:100%; height:100%; object-fit:cover; display:block; }

/* name sits on image bottom */
.tt-student-name{
  position:absolute; left:12px; bottom:10px; color:#fff; font-weight:600; font-size:16px;
  text-shadow:0 1px 2px rgba(0,0,0,.6); z-index:2;
}

/* overlay only over image area */
.tt-student-overlay{
  position:absolute; inset:0; background:linear-gradient(to top, rgba(0,0,0,.65), rgba(0,0,0,.15));
  color:#fff; opacity:0; transition:opacity .3s ease; display:flex; align-items:flex-end; padding:16px;
}
.tt-student-card:hover .tt-student-overlay{ opacity:1; }
.tt-overlay-content h4{ margin:0 0 6px; font-size:18px; }
.tt-overlay-content p{ font-size:14px; line-height:1.4; }

/* percentile is outside image -> no overlap */
.tt-student-percentile{
  padding:12px 16px 16px; font-weight:700; font-size:18px; color:#1b1ba3;
}

/* FIX the other hover too */
.tt-view-all:hover{ background:#f25c54; color:#fff; }

/* responsive */
@media (max-width:1024px){
  .tt-student-card{ flex:0 0 250px; width:250px; }
  .tt-media{ height:260px; }
}
@media (max-width:768px){
  .tt-student-card{ flex:0 0 240px; width:240px; }
  .tt-media{ height:240px; }
}
@media (max-width:480px){
  .tt-student-card{ flex:0 0 220px; width:220px; }
  .tt-media{ height:220px; }
  .tt-student-name{ font-size:13px; bottom:8px; left:10px; }
  .tt-student-percentile{ font-size:14px; padding:10px 12px 14px; }
}
